<template>
    <div class="goods-list">
        <router-link tag="div" :to="{name:'goodsinfo',params:{id:item.id}}" class="goods-item" v-for="(item,index) in goodsList" :key="index">
            <img  :src="item.img_url"/>
            <h1 class="title">{{item.title}}</h1>
            <div class="info">
                <p class="price">
                    <span class="new">￥{{item.sell_price}}</span>
                    <span class="old">￥{{item.market_price}}</span>
                </p>
                <p class="sell">
                    <span>热卖中</span>
                    <span>剩{{item.stock_quantity}}件</span>
                </p>
            </div>
        </router-link>
       <mt-button size="large" type="danger" @click="getMore">加载更多</mt-button>
     
    </div>
</template>
<script>
import { Toast } from 'mint-ui';
import api from '@/api/index.js'

export default {
    data() {
        return{
            pageNumber:1,
            goodsList:[]
        }
    },
    created() {
        this.getGoods();
    },
    methods: {
        getGoods(pageNumber) {
            let params = {
                pageindex:this.pageNumber
            }
            api.getGoods(params).then(res => {
                if(res.data.status === 0 ) {
                    // this.goodsList = res.data.message;
                     this.goodsList = this.goodsList.concat(res.data.message);
                } else {
                    Toast('请求商品列表失败~~');
                }
            })
        },
        getMore() {
            this.pageNumber ++;
            this.getGoods();
        }
    },
}
</script>
<style lang="scss" scoped>
.goods-list{
    display:flex;
    flex-wrap:wrap;
    padding:7px;
    justify-content:space-between;
   .goods-item{
        width:49%;
        border:1px solid #ccc;
        box-shadow:0 0 8px #ccc;
        margin:4px 0;
        padding:2px;
        display:flex;
        flex-direction:column;
        justify-content:space-between;
        min-height:293px;
        img{
            width:100%;
        }
        .title{
            font-size:14px;
        }
        .info{
            background-color:#eee;
            p{
                margin:0;
                padding:5px;
            }
            .price{
                .new{
                    color:red;
                    font-weight:bold;
                    font-size:16px;
                }
                .old{
                    text-decoration:line-through;
                    font-size:12px;
                    margin-left:10px;
                }
            }
            .sell{
                display:flex;
                justify-content:space-between;
                font-size:12px;
            }
        }
   }
}
</style>


